import { useEffect } from "react";
import { useState } from "react";
import SearchFilter from "react-filter-search";
import { Link } from "react-router-dom";
import { useRecoilState } from "recoil";
import styled from "styled-components";
import { ReactComponent as Search } from "../../assets/search.svg";
import { myFrigeAtom } from "../../atom";
import AllFrigeList from "./AllFrigeList";

let data = ["", "", "", "", ""];

// const FrigeContainer = styled.div`
//   width: 100vw;
//   height: 100vh;
//   padding: 0px 23px;
// `;

const FrigeSearchContainer = styled.div`
  width: 100%;
  padding: 70px 23px 0px 23px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05);
  background-color: white;
`;

const FrigeTitle = styled.p`
  font-weight: 500;
  font-size: 24px;
  line-height: 120%;
  letter-spacing: -0.05em;
  white-space: pre-wrap;
`;

const InputWrapper = styled.div`
  position: relative;
  height: 44px;
  width: 100%;
  background: #f1f1f1;
  border-radius: 10px;
  display: flex;
  align-items: center;
  padding-left: 16px;
  margin-bottom: 18px;
`;

const StyledInput = styled.input`
  position: absolute;
  width: 90%;
  height: 100%;
  padding-left: 30px;
  background: transparent;
  &:focus {
    outline: none;
  }
`;

const IngredientItemList = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  padding-bottom: 23px;
`;

const IngredientItem = styled.button`
  height: 37px;
  border: 1px solid #2e8cfe;
  border-radius: 50px;
  display: flex;
  align-items: center;
`;

const SelectedIngredientItem = styled.button`
  background: #2e8cfe;
  border: 1px solid #2e8cfe;
  border-radius: 50px;
  display: flex;
  align-items: center;
  height: 37px;
  color: white;
`;

const IngredientName = styled.p`
  margin: 10px 15px;
  font-weight: 500;
  font-size: 14px;
  line-height: 120%;
  letter-spacing: -0.03em;
`;

const SelectionCompleteBtn = styled.button`
  z-index: 99;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 85px;
  background: #2e8cfe;
  border-radius: 10px;
  width: 328px;
  height: 46px;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  align-items: center;
  text-align: center;
  letter-spacing: -0.03em;
  color: #ffffff;
  box-shadow: 0px 3px 10px #a9d0ff;
  &:disabled {
    background: #a9a9a9;
  }
`;

const FrigeGradient = styled.div`
  z-index: 2;
  width: 100%;
  height: 170px;
  position: fixed;
  bottom: 64px;
  background: linear-gradient(
    181.02deg,
    rgba(255, 255, 255, 0) 13.62%,
    #ffffff 55.49%
  );
`;

function Frige() {
  const [searchInput, setSearchInput] = useState("");
  const [myFrige, setMyFrige] = useRecoilState(myFrigeAtom);
  const [irdnt, setIrdnt] = useState([]);
  console.log(myFrige);

  useEffect(() => {
    (async () => {
      const response = await fetch(
        "https://naengpa.herokuapp.com/recipe/getIrdnt"
      );
      const json = await response.json();
      setIrdnt(json);
    })();
  }, []);
  console.log(irdnt);

  return (
    <>
      <FrigeSearchContainer>
        <FrigeTitle>  {"\n"}</FrigeTitle>
        <InputWrapper>
          <Search />
          <StyledInput
            className="border border-0 search-input"
            placeholder="   "
            value={searchInput}
            onChange={(e) => setSearchInput(e.target.value)}
          />
        </InputWrapper>
        <SearchFilter
          value={searchInput}
          data={irdnt}
          renderResults={(results) => (
            <IngredientItemList>
              {results.map((item) =>
                searchInput === "" ? null : (
                  <div>
                    {myFrige.indexOf(item.title) === -1 ? (
                      <IngredientItem
                        onClick={() => {
                          console.log(item.title);
                          setMyFrige((prev) => [...prev, item.title]);
                        }}
                      >
                        <IngredientName>{item.title}</IngredientName>
                      </IngredientItem>
                    ) : (
                      <SelectedIngredientItem
                        onClick={() =>
                          setMyFrige((prev) =>
                            [...prev].filter(
                              (element) => element !== item.title
                            )
                          )
                        }
                      >
                        <IngredientName>{item.title}</IngredientName>
                      </SelectedIngredientItem>
                    )}
                  </div>
                )
              )}
            </IngredientItemList>
          )}
        />
      </FrigeSearchContainer>
      <IngredientItemList>
        <AllFrigeList irdnt={irdnt} />
      </IngredientItemList>
      {myFrige.length === 0 ? (
        <SelectionCompleteBtn disabled> </SelectionCompleteBtn>
      ) : (
        <Link to={{ pathname: "/myfrige" }}>
          <SelectionCompleteBtn> </SelectionCompleteBtn>
        </Link>
      )}
      <FrigeGradient />
    </>
  );
}

export default Frige;
